<template>

</template>

<script>
    export default {
        name: 'modeling-element-base',
        props: {
            value: Object,
            definition: Object,
        },
        created: function () {

        },
        data: function () {
            return {
                _id: null,
                rotateMove: false,
                tmpWidth: 0,
                tmpHeight: 0,
            }
        },
        computed: {
            type() {
                return ''
            },
            code() {
                return ''
            },
            style: {
                get: function () {
                    var style;
                    //    .
                    if (this.value) {
                        if (this.value.elementView)
                            style = this.value.elementView.style;
                        else
                            style = this.value.relationView.style;
                    }

                    if (style) {
                        var jsonStyle = JSON.parse(style);
                        if ($.isEmptyObject(jsonStyle)) {
                            return this.defaultStyle;
                        } else {
                            return jsonStyle;
                        }
                    } else {
                        return this.defaultStyle;
                    }
                },
                set: function (val) {
                    if (this.value) {
                        if (this.value.elementView)
                            this.value.elementView.style = JSON.stringify(val);
                        else
                            this.value.relationView.style = JSON.stringify(val);
                    }
                }
            },

        },
        watch: {
            // 'value.drawer': function (newValue, oldValue) {
            //     var designer = this.getComponent('modeling-designer')
            //
            //     var me = this
            //     // designer.syncOthers(this.value);
            //
            //     this.aggregateList = []
            //     if (newValue == true) {
            //         designer.value.definition.forEach(function (temp) {
            //             if (temp._type == "org.uengine.modeling.model.Aggregate") {
            //                 console.log(temp)
            //                 me.aggregateList.push(temp.inputText);
            //             }
            //         })
            //     }
            // },
            "value.name": {
                handler: function (newVal, oldVal) {
                    var me = this
                    var designer = this.getComponent('modeling-designer')

                    me.rotateMove = true
                    if (me.value._type != 'org.uengine.modeling.model.Relation') {
                        me.value.elementView.x = me.value.elementView.x + 1
                        me.$nextTick(function () {
                            me.value.elementView.width = me.tmpWidth
                            me.value.elementView.height = me.tmpHeight
                            me.rotateMove = true
                            me.value.elementView.x = me.value.elementView.x - 1
                            me.$nextTick(function () {
                                me.value.elementView.width = me.tmpWidth
                                me.value.elementView.height = me.tmpHeight
                            })
                        })
                    }

                }
            },
            "value.elementView.width": {
                handler: function (newVal, oldVal) {
                    var me = this
                    if (me.rotateMove == true) {
                        me.tmpWidth = oldVal
                    }
                }
            },
            "value.elementView.height": {
                handler: function (newVal, oldVal) {
                    var me = this
                    if (me.rotateMove == true) {
                        me.tmpHeight = oldVal
                    }
                }
            },
        },
        mounted: function () {
            var me = this
            $(document).keydown((evt) => {
                if (evt.keyCode == 46 || evt.keyCode == 8) {
                    me.deleteActivity();
                }
            });
        },
        methods: {
            deleteActivity() {
                var me = this
                var designer = this.getComponent('modeling-designer')

                if(me.value.selected && !me.value.drawer) {
                    // new UndoRedo
                    if (designer.undoRedoIndex != designer.lastIndex) {
                        //undoRedo 
                        //
                        designer.undoRedoArray.splice(designer.undoRedoIndex + 1, designer.lastIndex - designer.undoRedoIndex);

                        designer.undoRedoIndex = designer.undoRedoIndex + 1
                        designer.lastIndex = designer.undoRedoIndex

                        designer.undoRedoArray.push(JSON.stringify(designer.value))

                    } else {
                        if (designer.undoRedoArray.length > designer.undoRedoStorage) {
                            designer.undoRedoArray.shift();
                        } else {
                            designer.undoRedoIndex = designer.undoRedoIndex + 1
                        }

                        designer.undoRedoArray.push(JSON.stringify(designer.value))
                    }

                    // designer.lastIndex = designer.undoRedoArray.length - 1



                    if(me.value._type =="org.uengine.modeling.model.BoundedContext") {
                        me.getBounded(me.value.name)
                    }else if(me.value._type != "org.uengine.modeling.model.Relation") {
                        if(me.value._type == "org.uengine.modeling.model.Event") {
                            designer.value.relation.some(function (relation,relationIdx) {
                                console.log(relation.to, me.value.elementView.id)
                                if(relation.from == me.value.elementView.id) {
                                    designer.value.relation[relationIdx] = null;
                                }
                            })
                            designer.value.relation = designer.value.relation.filter(n=>n);
                        } else if(me.value._type == "org.uengine.modeling.model.Policy") {
                            designer.value.relation.some(function (relation,relationIdx) {
                                if(relation.to == me.value.elementView.id) {
                                    designer.value.relation[relationIdx] = null;
                                }
                            })
                            designer.value.relation = designer.value.relation.filter(n=>n);
                        }

                        var totalLength = designer.value.definition.length
                        designer.value.definition = designer.value.definition.reduce((previousValue,currValue,currIndex,array) => {
                            if(currValue !=null)
                                if(me.value.elementView.id === currValue.elementView.id ) {
                                    array[currIndex] = null
                                    array[currIndex] = array[totalLength-1];
                                    array[totalLength-1] = null
                                    console.log("delete")
                                }

                            array = array.filter(n=>n);
                            return array
                        },[])



                    } else  {
                        designer.value.relation.some(function (relation,relationIdx) {
                            if(relation.to == me.value.to && relation.from == me.value.from) {
                                designer.value.relation[relationIdx] = null;
                            }
                        })
                        designer.value.relation = designer.value.relation.filter(n=>n);
                    }


                }
            },
            difference: function (object, base) {
                function changes(object, base) {
                    return _.transform(object, function (result, value, key) {
                        if (!_.isEqual(value, base[key])) {
                            result[key] = (_.isObject(value) && _.isObject(base[key])) ? changes(value, base[key]) : value;
                        }
                    });
                }

                return changes(object, base);
            },
            onLabelChanged: function (be, af) {
                console.log("onLabelChanged", be, af);
            },
            onRotateShape: function (me, angle) {
                this.value.elementView.angle = angle
            },
            connected: function () {
                console.log(this.value)
            },
            selectedActivity: function () {
                if (this.value) {
                    // var designer = this.getComponent('modeling-designer')
                    // designer.syncOthers(this.value);
                    this.value.selected = true
                }
                // this._selected = true;
            },
            deSelectedActivity: function () {
                // console.log('UnSelected')
                if (this.value) {
                    this.value.selected = false
                    if (this.value.drawer) {
                        this.value.drawer = false
                    }
                }
            },
            showProperty: function () {
                var designer = this.getComponent('modeling-designer')
                if (!this.value.editing) {
                    this.value.drawer = true;
                } else {
                    designer.snackbar = true
                }

            },
            getBounded(boundedName) {
                var designer = this.getComponent('modeling-designer')

                designer.value.definition.forEach(function (item, idx){
                    if(item.boundedContext &&  item.boundedContext == boundedName) {
                        console.log(item.eventToPolicy)
                        if(item.eventToPolicy != undefined) {
                            designer.value.relation.some(function (relation,relationIdx) {
                                console.log(relation.to, item.elementView.id)
                                if(relation.from == item.elementView.id || relation.to == item.elementView.id) {
                                    designer.value.relation[relationIdx] = null;
                                }
                            })
                            designer.value.relation = designer.value.relation.filter(n=>n);
                        }
                        designer.value.definition[idx] = null
                    }
                })
                designer.value.definition = designer.value.definition.filter(n=>n);

            },
            /**
             *     
             * @param groupElement
             * @param elements
             */
            onAddToGroup: function (groupElement, elements, eventOffset) {
                elements.forEach(function (element) {
                    var inner = false

                    groupElement.$parent.value.aggregates.some(function (tmp) {
                        if (tmp.elementView.id == element.$parent.value.elementView.id) {
                            return inner = true;
                        }
                    })

                    if (inner == false) {
                        if (groupElement.$parent.value._type == 'org.uengine.modeling.model.BoundedContext' && element.$parent.value._type == 'org.uengine.modeling.model.Aggregate') {
                            groupElement.$parent.value.aggregates.push(element.$parent.value)
                            // groupElement.$parent.value.boundedContext = element.$parent.value.name
                            // element.$parent.value.aggregates.push(groupElement.$parent.value)
                        }
                    }

                })
            },

            /**
             *     
             * @param groupElement
             * @param element
             */
            onAddedToGroup: function (groupElement, element, eventOffset) {
                var me = this;
                var designer = this.getComponent('modeling-designer')

                if (groupElement.tagName) {
                    // Canvas  
                    console.log("Out BoundedContext")
                    designer.value.definition.forEach(function (tmp) {
                        // console.log(tmp)
                        if (tmp._type == 'org.uengine.modeling.model.BoundedContext' && tmp.name == element.$parent.value.boundedContext) {
                            tmp.aggregates.some(function (boundedTmp, idx) {
                                if (boundedTmp.elementView.id == element.$parent.value.elementView.id) {
                                    tmp.aggregates = [
                                        ...tmp.aggregates.slice(0, idx),
                                        ...tmp.aggregates.slice(idx + 1)
                                    ]
                                }
                            })
                        }
                    })

                    element.$parent.value.boundedContext = ""

                } else {
                    console.log("Into BoundedContext")
                    // Bounded Context  
                    //     
                    if (groupElement.$parent.value._type == 'org.uengine.modeling.model.BoundedContext') {
                        element.$parent.value.boundedContext = ""
                        element.$parent.value.boundedContext = groupElement.$parent.value.name
                    }
                }

            },
            uuid: function () {
                function s4() {
                    return Math.floor((1 + Math.random()) * 0x10000)
                        .toString(16)
                        .substring(1);
                }

                return s4() + s4() + '-' + s4() + '-' + s4() + '-' +
                    s4() + '-' + s4() + s4() + s4();
            },
            refresh() {
                var me = this
                me.value.elementView.x = me.value.elementView.x + 1
                me.$nextTick(function () {
                    me.value.elementView.width = me.tmpWidth
                    me.value.elementView.height = me.tmpHeight
                    me.rotateMove = true
                    me.value.elementView.x = me.value.elementView.x - 1
                    me.$nextTick(function () {
                        me.value.elementView.width = me.tmpWidth
                        me.value.elementView.height = me.tmpHeight
                    })
                })
            },
            getComponent(componentName) {
                let component = null
                let parent = this.$parent
                while (parent && !component) {
                    if (parent.$options.name === componentName) {
                        component = parent
                    }
                    parent = parent.$parent
                }
                return component
            },
            onRemoveShape: function () {
                console.log("dddddddd")
            },
        }
    }
</script>


<style scoped lang="scss" rel="stylesheet/scss"></style>
